import React, {Component} from "react";
import {connect} from "react-redux";
import {getFixAppartmentNoValidation, isEarlierInstallationConsentNotAcceptedModalVisible} from "../../selectors";
import {dismissBackendValidationErrors, doCheckoutStep, gotoNextCheckoutStep, closeEarlierInstallationConsentNotAcceptedModal, acceptEarlierInstallationConsentNotAcceptedModal} from "../../actions/app";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {OraButton} from "eshop/components/OraCommonComponents";
import OraCheckoutAssistModeConfirmationPopup from "../assistMode/OraCheckoutAssistModeConfirmationPopup";

class AddressAppartmentNoInfoModalView extends Component{
    constructor(props){
        super(props)
    }

    toHTML(content) {
        return {__html: content};
    }

    checkOpenModal(){
        return (this.props.errors && this.props.errors.length>0) || this.props.isEarlierInstallationConsentNotAcceptedModalVisible
    }

    onClose() {
        this.props.dismiss();
    }

    onReject() {
        if(!this.props.isEarlierInstallationConsentNotAcceptedModalVisible) {
            this.props.dismiss();
        } else {
            this.props.closeEarlierInstallationConsentNotAcceptedModal();
        }
    }

    onAccept() {
        if(!this.props.isEarlierInstallationConsentNotAcceptedModalVisible) {
            this.props.actionNext();
        } else {
            this.props.earlierInstallationConsentNotAcceptedModalIsAccepted();
            this.props.closeEarlierInstallationConsentNotAcceptedModal();
            this.props.actionOnceAgain();
        }
    }

    render(){

        return (
            <OraModal id={'multicart-appartment-no-verification-modal'} showClose={false} open={this.checkOpenModal()} size="narrow" clickClose={false}>
                <div className="u-padding-all-m u-small-padding-s">
                    <div className="l-row">
                        <div className="l-col l-col-12 u-no-spacing">
                            <div className="opl-msg--box validation message" dangerouslySetInnerHTML={this.toHTML(this.props.content)}/>
                        </div>
                    </div>
                    <div className="l-row u-spacing-top-l">
                        <div className="l-col l-col-small-6 l-col-medium-6 l-col-4 large-offset-by-4">
                            <OraButton onClick={this.onReject.bind(this)} type="secondary" size="medium" className="u-w-100 opl-btn--yellow">{this.props.descriptions.leftButton}</OraButton>
                        </div>
                        <div className="l-col l-col-small-6 l-col-medium-6 l-col-4 ">
                            <OraButton onClick={this.onAccept.bind(this)} type="green" size="medium" className="u-w-100">{this.props.descriptions.rightButton}</OraButton>
                        </div>
                    </div>
                </div>
            </OraModal>
        )
    }
}

let mapStateToProps = state => ({
    errors: getFixAppartmentNoValidation(state),
    isEarlierInstallationConsentNotAcceptedModalVisible: isEarlierInstallationConsentNotAcceptedModalVisible(state)
});
let mapDispatchToProps = dispatch=> ({
    dismiss: () => dispatch(dismissBackendValidationErrors()),
    actionNext: () => dispatch(gotoNextCheckoutStep()),
    earlierInstallationConsentNotAcceptedModalIsAccepted: () => dispatch(acceptEarlierInstallationConsentNotAcceptedModal()),
    closeEarlierInstallationConsentNotAcceptedModal: () => dispatch(closeEarlierInstallationConsentNotAcceptedModal()),
    actionOnceAgain: () => dispatch(doCheckoutStep())
});

const AddressAppartmentNoInfoModal = connect(mapStateToProps, mapDispatchToProps)(AddressAppartmentNoInfoModalView);
export default AddressAppartmentNoInfoModal;



